.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding-bottom: 20px;
}

/* not sure why but this was difficult to do */
/* https://stackoverflow.com/questions/55625102/center-align-component-both-vertical-and-horizontal-in-react */
.formGroupCenter {
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
}

.name{
  color: white;
  text-align: right;
}

.jokes-col {
  padding-bottom: 20px;
}

img{
  /* width: 300px; */
  margin: 10px;
  width: 225px;
}

.form-label{
  font-size: 18px;
  padding-top: 3px;
  flex: 0 0 160px;
}

.category_label {
  flex: 0 0 100px;
}

.score_options{
  font-size: 18px;
  padding-top: 3px;
}

input[type=radio] {
  position: absolute;
}

.form-control {
  border-radius: .5rem;
  color: #6D757D;
}

.App-Component {
  width: 300px;
}

#combo-box-demo {
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
}

.sim_sc_display {
  display: inline-block;
  margin-right: 5px;
}

.info_icon:hover {
  filter: brightness(130%);
}
.star_hover {
  margin-top: 7px;
  display: inline-block;
}

.Highlight {
  background-color: #ffd54f;
  padding:0
}

.rating_stars {
  margin-top: 5px;
}

